body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

section {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 100px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow-x: hidden;
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fc4a55;
  transition: 0.5s all ease-in-out;
}

.bg::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 80%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
}

header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  position: relative;
  font-weight: 600;
  color: #fff;
  font-size: 2em;
}

.navigation {
  display: flex;
  position: relative;
  flex-direction: row;
}

.navigation li a {
  display: inline-block;
  color: #fff;
  font-weight: 400;
  margin-left: 10px;
  padding: 8px 15px;
  border-radius: 40px;
}

.navigation li a.active,
.navigation li a:hover {
  background: #fff;
  color: #333;
}

.textBox_line {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  color: #222;
}

.textBox_line.active {
  display: block;
}

.content {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-top: 80px;
}

.textBox {
  position: relative;
  max-width: 600px;
}

.textBox h2 {
  color: #fff;
  font-size: 4em;
  line-height: 1.2em;
  font-weight: 700;
}

.imgBox {
  position: relative;
  z-index: 1;
  width: 600px;
  display: flex;
  justify-content: flex-end;
}

.imgBox img {
  max-width: 400px;
}

.textBox_line {
  color: #fff;
  margin-top: 20px;
}

.btn {
  border: none;
  display: inline-block;
  margin-top: 25px;
  padding: 8px 20px;
  background: #fff;
  color: #333;
  cursor: pointer;
  border-radius: 40px;
  font-weight: 500;
  letter-spacing: 1px;
  text-decoration: none;
  font-size: 20px;
}

.thumb {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  display: flex;
}

.thumb li img {
  max-width: 60px;
}

.thumb li {
  display: inline-block;
  margin: 0 10px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  height: 70px;
  padding: 5px;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.25);
}

.thumb li.active {
  border: 2px solid white;
}

.thumb li::before {
  position: absolute;
  top: -200px;
  left: -100px;
  content: attr(data-text);
  white-space: nowrap;
  font-size: 8em;
  color: rgba(0, 0, 0, 0.05);
  opacity: 0;
  font-weight: 700;
  pointer-events: none;
}

.thumb li.active::before {
  opacity: 1;
}

.sci {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.sci li a {
  display: inline-block;
  margin: 5px 0;
  transform: scale(0.6);
  filter: invert(1);
}

@media (max-width: 991px) {
  header {
    padding: 20px;
  }

  section {
    padding: 20px;
    padding-bottom: 120px;
  }

  .bg::before {
    width: 75%;
  }

  .content {
    flex-direction: column;
    margin-top: 100px;
  }

  .textBox {
    max-width: 100%;
  }

  .textBox h2 {
    font-size: 2.5em;
    margin-bottom: 15px;
  }

  .imgBox {
    width: 100%;
    justify-content: center;
  }

  .thumb li {
    margin: 0 3px;
    height: 55px;
  }

  .thumb li img {
    max-width: 40px;
  }

  .thumb li::before {
    top: -120px;
    left: -160px;
    font-size: 6em;
  }

  .sci {
    background: rgba(0, 0, 0, 0.1);
    right: 0;
    z-index: 9;
    width: 50px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
}
@media (max-width: 767px) {
  .imgBox img {
    max-width: 300px;
  }

  .navigation {
    display: none;
  }

  .thumb li::before {
    top: -120px;
    left: -70px;
    font-size: 6em;
  }

  .bg::before {
    display: none;
  }

  .toggle {
    position: relative;
    width: 30px;
    height: 30px;
    cursor: pointer;
    background: url(../img/menu.webp);
    background-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 10000;
  }

  .toggle.active {
    position: fixed;
    background: url(../img/close.webp);
    background-size: 25px;
    background-position: center;
    background-repeat: no-repeat;
    right: 20px;
  }

  .navigation.active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fc4a55;
    z-index: 10;
    flex-direction: column;
  }

  .navigation.active li {
    margin: 5px 0;
  }

  .navigation.active li a {
    font-size: 1.2em;
  }
}
@media (max-width: 490px) {
  .textBox h2 {
    font-size: 1.5em;
  }

  .thumb li::before {
    top: -80px;
    left: 0px;
    font-size: 4em;
  }
}